.sidebar {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.sidebar .icon {
    width: 24px;
    height: 24px;
}

.sidebar .button {
    padding: 12px;
    margin: 8px 0px;
    width: 48px;
    height: 48px;
}

.sidebar .sidebar-header {
    height: 92px;
    flex-shrink: 0;
}

.sidebar .sidebar-header .sidebar-header-wrapper {
    margin-top: 12px;
    height: 73px;
}

.sidebar .sidebar-body {
    flex-grow: 1;
}

.sidebar .sidebar-footer {
    justify-content: flex-end;
}
