.home .home-header {
    background-color: rgb(250, 250, 250);
    height: 107px;
    padding: 10px 0px;
    border: 1px solid rgb(219, 219, 219);
    margin-bottom: 24px;
}



.home .home-header-list {
    padding: 0px 8px;
    height: 120px;
    width: auto;
    overflow: auto hidden;
    display: flex;
    flex-direction: row;
}

.home .home-header-item {
    top: 2px;
    padding: 0px 4px;
    width: 80px;
    height: 122px;
}

.home .home-header-content-image-wrapper {
    height: 56px;
    width: 56px;
    border-radius: 50%;
    margin-top: 4px;
    margin-bottom: 8px;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home .home-header-content-image-wrapper img {
    display: block;
    max-width: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.home .home-header-content-name {
    height: 16px;
    text-align: center;
}

.home .home-body-article-list {
    display: flex;
    flex-direction: column;
    width: 470px;
}


.home .home-body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.home .home-body-article {
    height: 600px;
    background-color: grey;
}